import {updateResult} from "../../containers/cbChecklist/checkListMethods";

const setValue = (checklistuuid,target,goal,result) => {
    updateResult(checklistuuid,target,goal,result ? 1 : 0);
}

const Checkbox = ({checklistuuid,goal,target,result}) => {
    return <input type="checkbox" checked={result} onChange={(evt)=>setValue(checklistuuid,target,goal,evt.currentTarget.checked)} />
}

export default Checkbox;