import { updateSiteModelChecklist } from "../../actions/cbChecklist_Actions";
import { updateAppSettings } from "../../actions/foundationActions";
import store from "../../store";

const newGoal = (newId,numOptions,checklistuuid) => {
    store.dispatch(updateSiteModelChecklist({newTarget:{parentId:newId,numOptions:numOptions,checklistuuid:checklistuuid}}));
    store.dispatch(updateAppSettings({"modalModuleName":"components/cbChecklist/TargetForm","modalTitle":"New Target","showDefaultModal":1}))
}


const AddTargetButton = ({parentId,numOptions,checklistuuid}) => {
    return <button className="btn btn-sm btn-info" onClick={()=>newGoal(parentId,numOptions,checklistuuid)}>+</button>
}

export default AddTargetButton;