let findNum=0;

export const checkWindow = () => {
    let checker = setInterval(()=>{
        if(window.SITEMODEL?.exists() || findNum===3){
            store.dispatch(updateSiteModelChecklist({windowSetup:true}));
            clearInterval(checker);
        }
        findNum++;
    },3000);
}

