const DisplayOrderChooser = ({length,val,action}) => {
    let options = Array.apply(null, Array(length)).map(function (x, i) { return i; });
   return  <select value={val} onChange={(evt)=>{action(evt.target.value)}}>
        <option value=""></option>
        {options.map((item)=>{
            return <option value={item}>{item+1}</option>
        })}
    </select>
}

export default DisplayOrderChooser;