import { updateSiteModelChecklist } from "../../actions/cbChecklist_Actions";
import { updateAppSettings } from "../../actions/foundationActions";
import { saveChecklistTarget } from "../../containers/cbChecklist/checkListMethods";
import store from "../../store";

const etarget = (target) => {
    store.dispatch(updateSiteModelChecklist({newTarget:target}));
    store.dispatch(updateAppSettings({showDefaultModal:1,"modalModuleName":"components/cbChecklist/TargetForm","modalTitle":"Edit Target"}));
}


const EditTarget = ({target}) => {
    return <button className="btn btn-sm btn-info" onClick={()=>{etarget(target)}}> Edit </button>
}

export default EditTarget;