import Checkbox from "./Checkbox";
import store from "../../store";
import {useEffect, useState} from "react";
import AddTargetButton from "./AddTargetButton";
import { randNum } from "../../utilities/utilityFunctions";
import DisplayOrderChooser from "./DisplayOrderChooser";
import { saveChecklistTarget } from "../../containers/cbChecklist/checkListMethods";
import DeleteTarget from "./DeleteTarget";
import EditTarget from "./EditTarget";


const setValue = (target,key,value) => {
    if(key==="displayorder"){
        target["updateOrder"]=1;
    }
    saveChecklistTarget(Object.assign({},target,{[key]:value}));
}




const TargetRow = ({target,goals,results,depth,numOptions}) => {
    let [showChildren,setShowChildren] = useState(false);
    let allTargets = store.getState().siteModelChecklist?.currentChecklistTargets ? store.getState().siteModelChecklist.currentChecklistTargets : [];
    
    let doneId = 0;
    let doneCount = 0;

    let filterGoals = goals?.filter((item)=>{
        if(item.label==='Done'){ doneId=item.id};
        return item.label?.length
    });

    
   

    let childs = allTargets?.filter((item)=>{
        return item.parentid===target?.id;
    })

    


    childs.forEach((item)=>{
        doneCount=doneCount + ( results[item?.id] && results[item?.id][doneId] ? parseInt(results[item?.id][doneId]) : 0) ;
    });

    let compDone=doneCount/childs.length;

    let bgColor = childs.length === 0 && ( results[target?.id] && results[target?.id][doneId] ? parseInt(results[target?.id][doneId]) : 0)===1
        ? "green"
        : compDone === 1
            ? "green"
            : compDone > .75
                ? "yellow"
                : "LightCoral"

    let indentAmount = 10*depth;
    let showAllChildren = store.getState().siteModelChecklist.showAllChildren ? store.getState().siteModelChecklist.showAllChildren : false;

    useEffect(()=>{
       setShowChildren(showAllChildren);
    },[showAllChildren])

    return <><tr>
        <td onClick={()=>setShowChildren(!showChildren)} style={{backgroundColor:bgColor}}>
            {childs.length ? showChildren ? "-" : "+" : null }
        </td>
        <td style={{paddingLeft:indentAmount + "%"}}>
            <div className="row">
                <div className="col-1"><AddTargetButton parentId={target.id} numOptions={childs.length} checklistuuid={target.checklistuuid} />    </div>
                <div className="col-10">{target?.testpackage}</div>
                <div className="col-1"></div>
            </div>
            
            
        </td>
        <td>
            <DisplayOrderChooser length={numOptions} val={target.displayorder} action={(newVal)=>{setValue(target,"displayorder",newVal)}} />
        </td>
        
        {filterGoals.map((item)=>{
            let displayValue = results[target?.id] && results[target?.id][item?.id] ? results[target?.id][item?.id] : 0;
            return <td><Checkbox checklistuuid={target.checklistuuid} goal={item.id} target={target.id} result={displayValue} key={item.id + target.testpackage}/></td>
        })}
        <td>
            <div className="row">
                <div className="col">
                    <EditTarget target={target} />
                </div>
                <div className="col">
                    <DeleteTarget target={target} />
                </div>
            </div>
            
        </td>
    </tr>

    {childs.length && showChildren ? childs.map((item)=>{
        return <TargetRow target={item} key={item.checklistuuid + item.testpackage + randNum(1,1000)} goals={goals} results={results} depth={depth+1} numOptions={childs.length} />
    }) : null}
        </>
}

export default TargetRow;